@font-face {
    font-family: 'HK Grotesk SemiBold';
    src: local('HK Grotesk'), url(../../Fonts/HKGrotesk-SemiBold.woff) format('woff');
}

@font-face {
    font-family: 'HK Grotesk Bold';
    src: local('HK Grotesk'), url(../../Fonts/HKGrotesk-Bold.woff) format('woff');
}

@font-face {
    font-family: 'HK Grotesk';
    src: local('HK Grotesk'), url(../../Fonts/HKGrotesk-Regular.woff) format('woff');
}

@font-face {
    font-family: 'HK Grotesk Bold';
    src: local('HK Grotesk'), url(../../Fonts/HKGrotesk-Black.woff) format('woff');
}

.well-page {
    background-color: #f2f2f2c2;
    margin-left: auto;
    margin-right: auto;
    padding: 4.5%;
    margin-top: calc(65px + 8vh);
    filter: drop-shadow(0 2mm 2mm #3b3b3b3f);
    margin-bottom: 0;
}

.well-bg {
    background-color: #ffffff;
    margin-top: 0;
    padding-top: 2%;
}

.well-empathise {
    background-color: #ddeff9;
    margin-top: 0;
    padding-top: 2%;
    padding-bottom: 2%;
}

.well-define {
    background-color: #ffffff;
    margin-top: 0;
    padding-top: 2%;
    padding-bottom: 2%;
}

.well-ideate {
    background-color: #1A5A87;
    margin-top: 0;
    padding-top: 2%;
    padding-bottom: 2%;
}

.well-refine {
    background-color: #f2f2f2;
    margin-top: 0;
    padding-top: 2%;
    padding-bottom: 10%;
    filter: drop-shadow(0 2mm 2mm #3b3b3b3f);
    margin-bottom: 0;
}

#ideate-title {
    color: #d3d3d3;
}

.ideation-container {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    background-color: #f2f2f2;
    padding: 1%;
    row-gap: 15px;
    align-items: top;
    justify-content: center;
    justify-items: center;
    align-content: center;
    max-width: 100%;
}

.draft-proto-container {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    background-color: #f2f2f2;
    padding: 1%;
    row-gap: 15px;
    align-items: top;
    justify-content: center;
    justify-items: center;
    align-content: center;
    max-width: 100%;
}

.paper-proto {
    display: grid;
    justify-items: center;
    grid-template-columns: repeat(auto-fit, minmax(min(600px, 100%), 1fr));
    margin-top: 5%;
}

.digi-proto {
    display: grid;
    justify-items: center;
    grid-template-columns: repeat(auto-fit, minmax(min(100%, 650px), 1fr));
}

.para-digi-cont {
    margin-top: 0;
}

.well-link {
    text-decoration: none;
    font-family: 'HK Grotesk SemiBold', sans-serif;
    color: #1a5a87;
}

/* HEADINGS */

#well-heading {
    color: #1a5a87;
    max-width: 70%;
    text-align: center;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 1.5%;
}

#well-subtitle {
    color: #7994aa;
    font-family: 'HK Grotesk SemiBold', sans-serif;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
    margin-top: 0;
    margin-bottom: 4%;
    max-width: 90%;
    font-size: 18px;
}

.well-h2 {
    font-family: 'HK Grotesk SemiBold', sans-serif;
    font-size: 28px;
    color: #1a5a87;
    margin-top: 3%;
    margin-bottom: 1%;
    max-width: 50%;
    text-align: left;
}

.well-h3 {
    font-family: 'HK Grotesk SemiBold', sans-serif;
    font-size: 36px;
    color: #1a5a87;
    padding-left: 1.5%;
    padding-right: 1.5%;
    padding-top: 3%;
    padding-bottom: 3%;
    max-width: 70vw;
    margin-left: auto;
    margin-right: auto;
}

.well-written-content {
    width: 80%;
    margin-left: auto;
    margin-right: auto;
}

.ulh4 {
    font-family: 'HK Grotesk SemiBold', sans-serif;
    font-size: 24px;
    color: #3b3b3b;
    margin-bottom: 0;
}

/* TEXT */
#ideation-p {
    max-width: 100%;
    color: #d3d3d3;
}

/* IMAGES */
#design-thinking {
    background-color: #FFFFFF;
    max-width: 1200px;
    margin: auto auto;
}

#design-thinking img{
    display: block;
    margin-left: auto;
    margin-right: auto;
    margin-top: 5%;
    width: 100%;
}

.ideas {
    max-width: 90%;
}

#proto4 {
    width: min(548px, 100%);
    height: min(388px, 100%);
    margin-bottom: 1em;
}

#proto5 {
    max-width: min(630px, 95%);

}

.para-proto {
    margin-top: 0;
}

.para-digi {
    margin-top: 0;
}

.well-final-imgs {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(180px, 1fr));
    row-gap: 15px;
    align-items: top;
    justify-content: center;
    justify-items: center;
    align-content: center;
    margin-top: 5%;
}

.well-final-img {
    width: 180px;
}

#well-infographic {
    max-width: 100%;
    display: block;
    margin-left: auto;
    margin-right: auto;
}